// Servicio de gestin de gestiones acadmicas

import { get, post, put, del } from './api'
import { MESSAGES, PAGINATION_CONFIG } from '../utils/constants'

export const gestionAcademicaService = {
  /**
   * Obtener lista de gestiones acadmicas con paginacin
   * @param {object} params - Parmetros de consulta
   * @returns {Promise<object>} Lista paginada de gestiones acadmicas
   */
  async getGestiones(params = {}) {
    try {
      const queryParams = {
        page: params.page || 1,
        per_page: params.per_page || PAGINATION_CONFIG.DEFAULT_PAGE_SIZE,
        search: params.search || '',
        sort_by: params.sort_by || 'ao',
        sort_order: params.sort_order || 'desc',
        ao: params.ao || '',
        periodo: params.periodo || '',
        activa: params.activa || ''
      }

      const response = await get('/gestiones-academicas', queryParams)
      
      console.log(' gestionAcademicaService.getGestiones - Response completa:', response)
      console.log(' gestionAcademicaService.getGestiones - response.data:', response.data)
      console.log(' gestionAcademicaService.getGestiones - response.data.success:', response.data?.success)
      console.log(' gestionAcademicaService.getGestiones - response.data.data:', response.data?.data)
      
      // El backend devuelve: { success: true, data: { data: [...], last_page: 1, ... } }
      // response.data es: { success: true, data: { data: [...], last_page: 1, ... } }
      if (response.data && response.data.success) {
        const paginatedData = response.data.data
        console.log(' gestionAcademicaService.getGestiones - Datos paginados:', paginatedData)
        console.log(' gestionAcademicaService.getGestiones - Total items:', paginatedData?.total)
        console.log(' gestionAcademicaService.getGestiones - Array data:', paginatedData?.data)
        
        return {
          success: true,
          data: paginatedData, // Este es el objeto paginado completo { data: [...], last_page: 1, ... }
          message: response.data.message
        }
      } else {
        console.error(' gestionAcademicaService.getGestiones - Response sin success:', response.data)
        return {
          success: false,
          message: response.data?.message || 'Error al obtener gestiones acadmicas',
          data: null
        }
      }
    } catch (error) {
      console.error(' Error en gestionAcademicaService.getGestiones:', error)
      console.error(' Response error:', error.response?.data)
      console.error(' Status:', error.response?.status)
      return {
        success: false,
        message: error.response?.data?.message || error.message || 'Error al obtener gestiones acadmicas',
        data: null
      }
    }
  },

  /**
   * Obtener gestin acadmica activa
   * @returns {Promise<object>} Gestin acadmica activa
   */
  async getGestionActiva() {
    try {
      const response = await get('/gestiones-academicas/activa')
      
      if (response.data.success) {
        return {
          success: true,
          data: response.data.data
        }
      } else {
        return {
          success: false,
          message: response.data.message || 'No hay gestin acadmica activa'
        }
      }
    } catch (error) {
      return {
        success: false,
        message: error.message || 'Error al obtener gestin acadmica activa'
      }
    }
  },

  /**
   * Obtener gestin acadmica por ID
   * @param {number} id - ID de la gestin acadmica
   * @returns {Promise<object>} Datos de la gestin acadmica
   */
  async getGestion(id) {
    try {
      const response = await get(`/gestiones-academicas/${id}`)
      
      if (response.data.success) {
        return {
          success: true,
          data: response.data.data
        }
      } else {
        return {
          success: false,
          message: response.data.message || 'Gestin acadmica no encontrada'
        }
      }
    } catch (error) {
      return {
        success: false,
        message: error.message || 'Error al obtener gestin acadmica'
      }
    }
  },

  /**
   * Crear nueva gestin acadmica
   * @param {object} data - Datos de la gestin acadmica
   * @returns {Promise<object>} Gestin acadmica creada
   */
  async createGestion(data) {
    try {
      const response = await post('/gestiones-academicas', data)
      
      if (response.data.success) {
        return {
          success: true,
          data: response.data.data,
          message: response.data.message || MESSAGES.SUCCESS.SAVE
        }
      } else {
        return {
          success: false,
          message: response.data.message || MESSAGES.ERROR.SAVE,
          errors: response.data.errors
        }
      }
    } catch (error) {
      return {
        success: false,
        message: error.response?.data?.message || error.message || MESSAGES.ERROR.SAVE,
        errors: error.response?.data?.errors
      }
    }
  },

  /**
   * Actualizar gestin acadmica
   * @param {number} id - ID de la gestin acadmica
   * @param {object} data - Datos actualizados
   * @returns {Promise<object>} Gestin acadmica actualizada
   */
  async updateGestion(id, data) {
    try {
      const response = await put(`/gestiones-academicas/${id}`, data)
      
      if (response.data.success) {
        return {
          success: true,
          data: response.data.data,
          message: response.data.message || MESSAGES.SUCCESS.UPDATE
        }
      } else {
        return {
          success: false,
          message: response.data.message || MESSAGES.ERROR.UPDATE,
          errors: response.data.errors
        }
      }
    } catch (error) {
      return {
        success: false,
        message: error.response?.data?.message || error.message || MESSAGES.ERROR.UPDATE,
        errors: error.response?.data?.errors
      }
    }
  },

  /**
   * Eliminar gestin acadmica
   * @param {number} id - ID de la gestin acadmica
   * @returns {Promise<object>} Respuesta de eliminacin
   */
  async deleteGestion(id) {
    try {
      const response = await del(`/gestiones-academicas/${id}`)
      
      if (response.data.success) {
        return {
          success: true,
          message: response.data.message || MESSAGES.SUCCESS.DELETE
        }
      } else {
        return {
          success: false,
          message: response.data.message || MESSAGES.ERROR.DELETE
        }
      }
    } catch (error) {
      return {
        success: false,
        message: error.response?.data?.message || error.message || MESSAGES.ERROR.DELETE
      }
    }
  },

  /**
   * Activar una gestin acadmica
   * @param {number} id - ID de la gestin acadmica
   * @returns {Promise<object>} Gestin acadmica activada
   */
  async activarGestion(id) {
    try {
      const response = await put(`/gestiones-academicas/${id}/activar`)
      
      if (response.data.success) {
        return {
          success: true,
          data: response.data.data,
          message: 'Gestin acadmica activada exitosamente'
        }
      } else {
        return {
          success: false,
          message: response.data.message || 'Error al activar la gestin acadmica'
        }
      }
    } catch (error) {
      return {
        success: false,
        message: error.message || 'Error al activar la gestin acadmica'
      }
    }
  },

  /**
   * Exportar gestiones acadmicas a CSV/Excel
   * @param {object} params - Parmetros de filtrado
   * @returns {Promise<object>} Lista de gestiones acadmicas para exportar
   */
  async exportarGestiones(params = {}) {
    try {
      const queryParams = {
        page: 1,
        per_page: 10000,
        search: params.search || '',
        ao: params.ao || '',
        periodo: params.periodo || '',
        activa: params.activa || ''
      }

      const response = await get('/gestiones-academicas', queryParams)
      
      if (response.data && response.data.success && response.data.data) {
        return {
          success: true,
          data: response.data.data.data || []
        }
      } else {
        return {
          success: false,
          message: response.data?.message || 'Error al exportar gestiones acadmicas'
        }
      }
    } catch (error) {
      return {
        success: false,
        message: error.response?.data?.message || error.message || 'Error al exportar gestiones acadmicas'
      }
    }
  },

  /**
   * Importar gestiones acadmicas desde archivo Excel/CSV
   * @param {File} file - Archivo a importar
   * @returns {Promise<object>} Resultado de la importacin
   */
  async importarGestiones(file) {
    try {
      const formData = new FormData()
      formData.append('file', file)

      const response = await post('/gestiones-academicas/import', formData)
      
      if (response.data.success) {
        return {
          success: true,
          data: response.data.data,
          message: response.data.message || 'Gestiones acadmicas importadas exitosamente'
        }
      } else {
        return {
          success: false,
          message: response.data.message || 'Error al importar gestiones acadmicas',
          errors: response.data.errors
        }
      }
    } catch (error) {
      return {
        success: false,
        message: error.response?.data?.message || error.message || 'Error al importar gestiones acadmicas',
        errors: error.response?.data?.errors
      }
    }
  }
}

