// Configuracin de Axios para el sistema acadmico

import axios from 'axios'
import toast from 'react-hot-toast'
import { APP_CONFIG, MESSAGES } from '../utils/constants'

// Crear instancia de Axios
const api = axios.create({
  baseURL: APP_CONFIG.apiUrl,
  timeout: 30000, // 30 segundos
  headers: {
    'Content-Type': 'application/json',
    'Accept': 'application/json',
    'X-Requested-With': 'XMLHttpRequest'
  }
})

// Interceptor para agregar token automticamente
api.interceptors.request.use(
  (config) => {
    const token = localStorage.getItem('token')
    if (token) {
      config.headers.Authorization = `Bearer ${token}`
    }
    
    // Log de peticiones en modo debug
    if (APP_CONFIG.debugMode) {
      console.log(' API Request:', {
        method: config.method?.toUpperCase(),
        url: config.url,
        data: config.data,
        params: config.params
      })
    }
    
    return config
  },
  (error) => {
    console.error(' Request Error:', error)
    return Promise.reject(error)
  }
)

// Interceptor para manejar respuestas y errores
api.interceptors.response.use(
  (response) => {
    // Log de respuestas en modo debug
    if (APP_CONFIG.debugMode) {
      console.log(' API Response:', {
        status: response.status,
        url: response.config.url,
        data: response.data
      })
    }
    
    return response
  },
  async (error) => {
    const originalRequest = error.config
    
    // Solo loguear errores si no son errores de red/CORS esperados
    const isNetworkError = !error.response && (error.code === 'ERR_NETWORK' || error.message?.includes('Network Error'))
    const isCORSError = error.message?.includes('CORS') || error.message?.includes('blocked')
    
    // No loguear errores de red/CORS repetidos para evitar spam en consola
    if (!isNetworkError && !isCORSError) {
      console.error(' API Error:', {
        status: error.response?.status,
        url: error.config?.url,
        message: error.message,
        data: error.response?.data
      })
    }
    
    // Manejar error 401 (No autorizado)
    if (error.response?.status === 401) {
      // Solo limpiar token si no es una peticin de perfil (checkAuth)
      // El checkAuth manejar la limpieza de sesin si es necesario
      const isProfileRequest = originalRequest.url?.includes('/auth/perfil')
      
      if (!isProfileRequest) {
        // Limpiar token y redirigir al login en caso de 401
        localStorage.removeItem('token')
        sessionStorage.clear()
        
        // Evitar redirigir si ya estamos en login o si es una peticin de login
        if (!originalRequest.url?.includes('/auth/login') && !originalRequest.url?.includes('/auth/estudiante/login') && !originalRequest.url?.includes('/auth/admin/login') && !window.location.pathname.includes('/login')) {
          // Limpiar cualquier cach
          if (window.queryClient) {
            window.queryClient.clear()
          }
          
          // Redirigir segn la ruta actual
          const currentPath = window.location.pathname
          if (currentPath.startsWith('/estudiante')) {
            window.location.href = '/estudiante/login'
          } else if (currentPath.startsWith('/docente')) {
            window.location.href = '/docente/login'
          } else {
            window.location.href = '/login'
          }
        }
      } else {
        // Para peticiones de perfil, solo limpiar el token pero no redirigir
        // El AuthContext manejar la redireccin
        localStorage.removeItem('token')
        sessionStorage.clear()
      }
      
      return Promise.reject(error)
    }
    
    // Manejar otros errores
    const errorMessage = getErrorMessage(error)
    
    // No mostrar toast para errores de red/CORS en notificaciones (se manejan silenciosamente)
    const isNotificationsRequest = originalRequest.url?.includes('/notificaciones')
    const shouldShowToast = !originalRequest.silent && !(isNetworkError && isNotificationsRequest)
    
    if (shouldShowToast) {
      toast.error(errorMessage)
    }
    
    return Promise.reject({
      ...error,
      message: errorMessage,
      status: error.response?.status,
      data: error.response?.data
    })
  }
)

/**
 * Obtiene el mensaje de error apropiado
 * @param {Error} error - Error de la peticin
 * @returns {string} Mensaje de error
 */
const getErrorMessage = (error) => {
  // Error de red (sin respuesta del servidor)
  if (!error.response) {
    // Verificar si es un error de CORS
    if (error.message?.includes('CORS') || error.message?.includes('blocked')) {
      return 'Error de CORS: El servidor no permite solicitudes desde este origen'
    }
    if (error.code === 'ERR_NETWORK' || error.message?.includes('Network Error')) {
      return 'Error de conexin: No se pudo conectar con el servidor'
    }
    return error.message || MESSAGES.ERROR.NETWORK
  }
  
  const status = error.response.status
  const data = error.response.data
  
  // Mensajes especficos del backend (prioridad alta)
  if (data?.message) {
    return data.message
  }
  
  // Errores de validacin (422) - mostrar el primer error o todos
  if (status === 422 && data?.errors) {
    const errorMessages = []
    if (typeof data.errors === 'object') {
      Object.keys(data.errors).forEach(key => {
        const fieldErrors = Array.isArray(data.errors[key]) 
          ? data.errors[key] 
          : [data.errors[key]]
        fieldErrors.forEach(msg => errorMessages.push(`${key}: ${msg}`))
      })
      if (errorMessages.length > 0) {
        return errorMessages.length === 1 
          ? errorMessages[0]
          : `Errores de validacin: ${errorMessages.slice(0, 3).join(', ')}${errorMessages.length > 3 ? '...' : ''}`
      }
    }
    return 'Datos de validacin incorrectos'
  }
  
  // Mensajes por cdigo de estado
  switch (status) {
    case 400:
      return data?.errors ? (typeof data.errors === 'string' ? data.errors : 'Datos invlidos') : 'Solicitud incorrecta'
    case 401:
      return data?.message || MESSAGES.ERROR.UNAUTHORIZED
    case 403:
      return data?.message || MESSAGES.ERROR.FORBIDDEN
    case 404:
      return data?.message || MESSAGES.ERROR.NOT_FOUND
    case 422:
      return data?.message || 'Datos de validacin incorrectos'
    case 429:
      return 'Demasiadas solicitudes. Intenta ms tarde'
    case 500:
      return data?.message || data?.error || MESSAGES.ERROR.SERVER_ERROR
    case 502:
      return 'Servidor no disponible'
    case 503:
      return 'Servicio temporalmente no disponible'
    default:
      return data?.message || data?.error || MESSAGES.ERROR.SERVER_ERROR
  }
}

/**
 * Realiza una peticin GET
 * @param {string} url - URL de la peticin
 * @param {object} params - Parmetros de consulta
 * @param {object} config - Configuracin adicional
 * @returns {Promise} Respuesta de la peticin
 */
export const get = (url, params = {}, config = {}) => {
  return api.get(url, { params, ...config })
}

/**
 * Realiza una peticin POST
 * @param {string} url - URL de la peticin
 * @param {object} data - Datos a enviar
 * @param {object} config - Configuracin adicional
 * @returns {Promise} Respuesta de la peticin
 */
export const post = (url, data = {}, config = {}) => {
  return api.post(url, data, config)
}

/**
 * Realiza una peticin PUT
 * @param {string} url - URL de la peticin
 * @param {object} data - Datos a enviar
 * @param {object} config - Configuracin adicional
 * @returns {Promise} Respuesta de la peticin
 */
export const put = (url, data = {}, config = {}) => {
  return api.put(url, data, config)
}

/**
 * Realiza una peticin PATCH
 * @param {string} url - URL de la peticin
 * @param {object} data - Datos a enviar
 * @param {object} config - Configuracin adicional
 * @returns {Promise} Respuesta de la peticin
 */
export const patch = (url, data = {}, config = {}) => {
  return api.patch(url, data, config)
}

/**
 * Realiza una peticin DELETE
 * @param {string} url - URL de la peticin
 * @param {object} config - Configuracin adicional
 * @returns {Promise} Respuesta de la peticin
 */
export const del = (url, config = {}) => {
  return api.delete(url, config)
}

/**
 * Realiza una peticin con archivos (multipart/form-data)
 * @param {string} url - URL de la peticin
 * @param {FormData} formData - Datos del formulario
 * @param {object} config - Configuracin adicional
 * @returns {Promise} Respuesta de la peticin
 */
export const upload = (url, formData, config = {}) => {
  return api.post(url, formData, {
    headers: {
      'Content-Type': 'multipart/form-data'
    },
    ...config
  })
}

/**
 * Realiza una peticin de descarga de archivo
 * @param {string} url - URL de la peticin
 * @param {object} params - Parmetros de consulta
 * @param {string} filename - Nombre del archivo
 * @returns {Promise} Respuesta de la peticin
 */
export const download = (url, params = {}, filename = 'download') => {
  return api.get(url, {
    params,
    responseType: 'blob'
  }).then(response => {
    const blob = new Blob([response.data])
    const downloadUrl = window.URL.createObjectURL(blob)
    const link = document.createElement('a')
    link.href = downloadUrl
    link.download = filename
    document.body.appendChild(link)
    link.click()
    document.body.removeChild(link)
    window.URL.revokeObjectURL(downloadUrl)
  })
}

/**
 * Realiza mltiples peticiones en paralelo
 * @param {Array} requests - Array de peticiones
 * @returns {Promise} Respuestas de las peticiones
 */
export const all = (requests) => {
  return Promise.all(requests)
}

/**
 * Realiza mltiples peticiones en paralelo con manejo de errores
 * @param {Array} requests - Array de peticiones
 * @returns {Promise} Respuestas de las peticiones
 */
export const allSettled = (requests) => {
  return Promise.allSettled(requests)
}

/**
 * Configuracin de timeout personalizado
 * @param {number} timeout - Timeout en milisegundos
 * @returns {object} Configuracin de Axios
 */
export const withTimeout = (timeout) => {
  return { timeout }
}

/**
 * Configuracin para peticin silenciosa (sin toast de error)
 * @returns {object} Configuracin de Axios
 */
export const silent = () => {
  return { silent: true }
}

/**
 * Configuracin para peticin con retry automtico
 * @param {number} retries - Nmero de reintentos
 * @param {number} delay - Delay entre reintentos en ms
 * @returns {object} Configuracin de Axios
 */
export const withRetry = (retries = 3, delay = 1000) => {
  return { retries, retryDelay: delay }
}

// Exportar la instancia de Axios para uso directo
export default api