// Servicio de gestin de bitcora

import { get } from './api'

export const bitacoraService = {
  /**
   * Obtener lista de registros de bitcora con paginacin
   * @param {object} params - Parmetros de consulta
   * @returns {Promise<object>} Lista paginada de registros
   */
  async getBitacora(params = {}) {
    try {
      const queryParams = {
        page: params.page || 1,
        per_page: params.per_page || 50,
        usuario_id: params.usuario_id || '',
        tabla: params.tabla || '',
        transaccion: params.transaccion || '',
        fecha_desde: params.fecha_desde || '',
        fecha_hasta: params.fecha_hasta || '',
        recientes: params.recientes || false
      }

      // Limpiar parmetros vacos
      Object.keys(queryParams).forEach(key => {
        if (queryParams[key] === '' || queryParams[key] === false || queryParams[key] === null) {
          delete queryParams[key]
        }
      })

      const response = await get('/admin/bitacora', queryParams)
      
      if (response.data.success) {
        return {
          success: true,
          data: response.data.data,
          message: response.data.message
        }
      } else {
        return {
          success: false,
          message: response.data.message || 'Error al obtener registros de bitcora'
        }
      }
    } catch (error) {
      return {
        success: false,
        message: error.response?.data?.message || error.message || 'Error al obtener registros de bitcora'
      }
    }
  },

  /**
   * Obtener registro de bitcora por ID
   * @param {number} id - ID del registro
   * @returns {Promise<object>} Registro de bitcora
   */
  async getRegistro(id) {
    try {
      const response = await get(`/admin/bitacora/${id}`)
      
      if (response.data.success) {
        return {
          success: true,
          data: response.data.data
        }
      } else {
        return {
          success: false,
          message: response.data.message || 'Registro no encontrado'
        }
      }
    } catch (error) {
      return {
        success: false,
        message: error.response?.data?.message || error.message || 'Error al obtener registro'
      }
    }
  },

  /**
   * Obtener estadsticas de bitcora
   * @param {object} params - Parmetros de consulta
   * @returns {Promise<object>} Estadsticas
   */
  async getEstadisticas(params = {}) {
    try {
      const queryParams = {
        fecha_desde: params.fecha_desde || '',
        fecha_hasta: params.fecha_hasta || ''
      }

      // Limpiar parmetros vacos
      Object.keys(queryParams).forEach(key => {
        if (queryParams[key] === '' || queryParams[key] === null) {
          delete queryParams[key]
        }
      })

      const response = await get('/admin/bitacora/estadisticas', queryParams)
      
      if (response.data.success) {
        return {
          success: true,
          data: response.data.data
        }
      } else {
        return {
          success: false,
          message: response.data.message || 'Error al obtener estadsticas'
        }
      }
    } catch (error) {
      return {
        success: false,
        message: error.response?.data?.message || error.message || 'Error al obtener estadsticas'
      }
    }
  }
}

